import React, { Fragment } from "react";
import { MdArrowBack, MdSchool } from "react-icons/md";
import { connect } from "react-redux";
import {
  SchoolData,
  Auth,
  Basic,
  FC_GetSchoolsByDistrict,
  FC_GetAllPositions,
  FC_GetSchoolPositions,
  FC_AllSchoolHierarchyDetails,
  SchoolHierarchyStore,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import AddSchoolPositions from "../../components/SchoolPositions/AddSchoolPositions";
import { StaffListComponent } from "../../components/StaffListComponent/StaffListComponent";
import StudyHierarchy from "../../components/StudyHierarchy/StudyHierarchy";
import { StoreState } from "../../reducers";
import { SchoolPositionApiResponse } from "../../types/HeadTeacherRequesTypes";
import { RenderPageAccess, UsersAccess } from "../../utils/access";
import { search } from "../../utils/functions";
import {
  PositionsOnSchool,
  SchoolPositionsCombination,
} from "../Head-Teacher/Request/HeadTeacherRequestToDee";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  FC_GetAllPositions: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
  schoolHierarchyStore: SchoolHierarchyStore;
  FC_AllSchoolHierarchyDetails: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
}

interface AppState {
  loading: boolean;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "update-error"
      | null;
    msg: string;
  };
  data: SchoolData[];
  loading_data: boolean;
  selected_level: string;
  search: string;
  schoolPositionsShow: {
    school_code: string;
    school_id: string;
    school: SchoolData;
    school_positions: PositionsOnSchool[];
  } | null;
  selected_school: SchoolData | null;
  school_tab: "details" | "staff-list" | "post-list" | "subjects" | null;
}

class _SchoolsList extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      data: [],
      error: {
        target: null,
        msg: "",
      },
      loading_data: false,
      selected_level: "",
      search: "",
      schoolPositionsShow: null,
      selected_school: null,
      school_tab: "details",
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  componentDidMount = () => {
    RenderPageAccess(
      this.props.auth.role?.access,
      UsersAccess.VIEW_SCHOOLS_LIST,
      () => {
        this.startApp();
      }
    );
    // Check if positions Available
    if (this.props.basic.positions === null) {
      this.props.FC_GetAllPositions((status: boolean, msg: string) => {
        console.log(status);
      });
    }
    // Get all hierarchy
    if (this.props.schoolHierarchyStore.hierarchy === null) {
      this.props.FC_AllSchoolHierarchyDetails(
        (status: boolean, msg: string) => {}
      );
    }
  };

  startApp = () => {
    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main",
          msg: "Failed to load data, please reload data",
        },
      });
    } else if (this.props.auth.district === null) {
      this.setState({
        error: {
          target: "main",
          msg: "No district assigned to you",
        },
      });
    } else {
      let district_id = this.props.auth.district.district_code;
      FC_GetSchoolsByDistrict(district_id, (status, data, msg) => {
        if (status === true && data !== null) {
          console.log({ status, data, msg });
          this.setState({
            loading_data: false,
            data: data,
          });
        } else {
          this.setState({
            loading_data: false,
            error: {
              msg: msg,
              target: "requests-error",
            },
          });
        }
      });
    }
  };

  selectSchool = (school: SchoolData | null) =>
    this.setState({ selected_school: school });

  render() {
    let SchoolsList: SchoolData[] = search(this.state.data, this.state.search, {
      school_name: true,
      school_category: true,
      school_code: true,
      region_code: true,
      school_status: true,
    });

    if (this.state.error.target === "main")
      return (
        <DashboardContainer>
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        </DashboardContainer>
      );

    if (
      this.state.loading_data === true ||
      this.props.schoolHierarchyStore.hierarchy === null
    )
      return (
        <DashboardContainer>
          <div className="mx-auto p-8">
            <Loading />
          </div>
        </DashboardContainer>
      );

    if (this.state.selected_school !== null)
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="py-2">
            <div className="flex items-center">
              <div className="pr-3">
                <Button
                  onClick={this.selectSchool.bind(this.componentDidCatch, null)}
                >
                  <MdArrowBack className="text-2xl" />
                </Button>
              </div>
              <div className="">
                <p>Shool name</p>
                <Heading>{this.state.selected_school.school_name}</Heading>
              </div>
            </div>
          </div>

          <div className="my-2">
            <Button
              className={`px-4 mr-1 ${
                this.state.school_tab === "details" ? "" : "opacity-60"
              }`}
              theme="primary"
              onClick={() => this.setState({ school_tab: "details" })}
            >
              Details
            </Button>
            <Button
              className={`px-4 mr-1 ${
                this.state.school_tab === "staff-list" ? "" : "opacity-60"
              }`}
              theme="primary"
              onClick={() => this.setState({ school_tab: "staff-list" })}
            >
              Staff list
            </Button>
            <Button
              className={`px-4 mr-1 ${
                this.state.school_tab === "subjects" ? "" : "opacity-60"
              }`}
              theme="primary"
              onClick={() => this.setState({ school_tab: "subjects" })}
            >
              School subjects
            </Button>
            <Button
              theme="primary"
              onClick={() => {
                if (
                  this.state.selected_school &&
                  this.props.basic.academic_calender
                ) {
                  this.setState({ school_tab: "post-list" });
                }
              }}
              className={`px-4 mr-1 ${
                this.state.school_tab === "post-list" ? "" : "opacity-60"
              }`}
            >
              Positions
            </Button>
          </div>
          <div className="p-2 mt-2">
            {this.state.school_tab === "details" && (
              <div className="flex">
                <div className="pr-4">
                  <MdSchool className="text-9xl" />
                </div>
                <div className="p-3 text-lg">
                  <div className="flex p-1">
                    <div className="">
                      <span>School category:</span>
                    </div>
                    <div className="">
                      <b>{this.state.selected_school.school_category}</b>
                    </div>
                  </div>
                  <div className="flex p-1">
                    <div className="">
                      <span>School code:</span>
                    </div>
                    <div className="">
                      <b>{this.state.selected_school.school_code}</b>
                    </div>
                  </div>
                  <div className="flex p-1">
                    <div className="">
                      <span>School status:</span>
                    </div>
                    <div className="">
                      <b>{this.state.selected_school.school_status}</b>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <div
              className={`${
                this.state.school_tab === "staff-list" ? "" : "hidden"
              }`}
            >
              <StaffListComponent
                school_code={this.state.selected_school.school_code}
              />
            </div>

            {this.state.school_tab === "subjects" &&
              this.state.selected_school !== null &&
              this.props.schoolHierarchyStore.hierarchy !== null && (
                <StudyHierarchy
                  school={{
                    school_code: this.state.selected_school.school_code,
                    school_name: this.state.selected_school.school_name,
                    schoolcategory: this.state.selected_school.school_category,
                    schoolstatus: this.state.selected_school.school_status,
                    regioncode: this.state.selected_school.region_code,
                  }}
                  allHierarchy={this.props.schoolHierarchyStore.hierarchy}
                  fixedHeight={true}
                />
              )}

            {this.state.school_tab === "post-list" && (
              <div className="">
                {this.state.schoolPositionsShow !== null &&
                  this.props.basic.positions !== null && (
                    <AddSchoolPositions
                      auth={this.props.auth}
                      positions={this.props.basic.positions}
                      schoolPositions={
                        this.state.schoolPositionsShow.school_positions
                      }
                      school={this.state.schoolPositionsShow.school}
                      appendPositionsToSchool={(
                        schoolPositions: SchoolPositionApiResponse[]
                      ) => {
                        if (
                          this.state.schoolPositionsShow &&
                          this.props.basic.academic_calender
                        ) {
                          FC_GetSchoolPositions(
                            this.state.schoolPositionsShow.school_code,
                            this.props.basic.academic_calender.academic_year_id,
                            (
                              status: boolean,
                              data: SchoolPositionsCombination | null,
                              msg: string
                            ) => {
                              if (data !== null) {
                                this.setState({ loading_data: true });
                                if (status === true) {
                                  this.state.schoolPositionsShow &&
                                    this.setState({
                                      schoolPositionsShow: {
                                        school_code:
                                          this.state.schoolPositionsShow
                                            .school_code,
                                        school_id:
                                          this.state.schoolPositionsShow
                                            .school_id,
                                        school_positions: data.school_positions,
                                        school:
                                          this.state.schoolPositionsShow.school,
                                      },
                                    });
                                  this.setState({
                                    loading_data: false,
                                  });
                                }
                              }
                            }
                          );
                        }
                      }}
                      removePositionsToSchool={(
                        school_has_position_id: string
                      ) => {
                        if (
                          this.state.schoolPositionsShow &&
                          this.props.basic.academic_calender
                        ) {
                          FC_GetSchoolPositions(
                            this.state.schoolPositionsShow.school_code,
                            this.props.basic.academic_calender.academic_year_id,
                            (
                              status: boolean,
                              data: SchoolPositionsCombination | null,
                              msg: string
                            ) => {
                              if (data !== null) {
                                this.setState({ loading_data: true });
                                if (status === true) {
                                  this.state.schoolPositionsShow &&
                                    this.setState({
                                      schoolPositionsShow: {
                                        school_code:
                                          this.state.schoolPositionsShow
                                            .school_code,
                                        school_id:
                                          this.state.schoolPositionsShow
                                            .school_id,
                                        school_positions: data.school_positions,
                                        school:
                                          this.state.schoolPositionsShow.school,
                                      },
                                    });
                                  this.setState({
                                    loading_data: false,
                                  });
                                }
                              }
                            }
                          );
                        }
                      }}
                      close={() => this.setState({ schoolPositionsShow: null })}
                    />
                  )}
              </div>
            )}
          </div>
        </DashboardContainer>
      );

    return (
      <Fragment>
        <>
          {this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error.msg}
            </Alert>
          )}

          <DashboardContainer>
            {/* {this.state.error.target === "main" && (
            <Alert theme="danger">{this.state.error.msg}</Alert>
          )} */}
            {this.state.error.target === "loading-update-error" && (
              <Alert theme="danger">{this.state.error.msg}</Alert>
            )}

            <Heading>
              {this.props.auth.district?.district_name} district's Schools
            </Heading>
            <div className="flex mt-2">
              <div className="flex-1">
                <p className="opacity-60 -mt-1">
                  Total: <b>{this.state.data.length}</b>
                </p>
              </div>

              <div className=""></div>
            </div>

            <div className="mt-3">
              <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
                <div className="flex-1 pr-2">
                  <TextInput
                    error=""
                    label="Search"
                    name=""
                    onChange={(e) => this.setState({ search: e.target.value })}
                    placeholder="Search by district name"
                    value={this.state.search}
                    size="sm"
                  />
                </div>
                <div className=""></div>
              </div>
              <table className="w-full text-left table-auto">
                <thead>
                  <tr>
                    <th>#</th>
                    <th>School code</th>
                    <th>School name</th>
                    <th>Category</th>
                    <th>Region Code</th>
                    <th>Status</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {SchoolsList.length <= 0 ? (
                    <tr>
                      <td
                        colSpan={8}
                        className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                      >
                        <Heading size="md" className="opacity-60 font-light">
                          No results found
                        </Heading>
                      </td>
                    </tr>
                  ) : (
                    SchoolsList.map((req, i) => {
                      // let Qualification = this.props.basic.qualifications.find(
                      //   (itm) => itm.qualification_id === req.
                      // );
                      return (
                        <Fragment key={i}>
                          <tr className="border rounded hover:bg-blue-500 hover:bg-opacity-20 group">
                            <td className="p-2 border" key={i}>
                              {i + 1}
                            </td>
                            <td className="p-2 border">{req.school_code}</td>
                            <td className="p-2 border">{req.school_name}</td>
                            <td className="p-2 border">
                              {req.school_category}
                            </td>
                            <td className="p-2 border">{req.region_code}</td>
                            <td className="p-2 border">{req.school_status}</td>
                            <td className="flex flex-row items-center justify-right p-1">
                              <Button
                                size="sm"
                                className="w-full"
                                theme="accent-light"
                                onClick={() => {
                                  this.selectSchool(req);
                                  if (this.props.basic.academic_calender) {
                                    FC_GetSchoolPositions(
                                      req.school_code,
                                      this.props.basic.academic_calender
                                        .academic_year_id,
                                      (
                                        status: boolean,
                                        data: SchoolPositionsCombination | null,
                                        msg: string
                                      ) => {
                                        if (data !== null) {
                                          this.setState({ loading_data: true });
                                          if (status === true && req !== null) {
                                            this.setState({
                                              schoolPositionsShow: {
                                                school_code: req?.school_code,
                                                school_id: req.school_id,
                                                school_positions:
                                                  data.school_positions,
                                                school: req,
                                              },
                                            });
                                            this.setState({
                                              loading_data: false,
                                            });
                                          }
                                        }
                                      }
                                    );
                                  }
                                }}
                              >
                                Select
                              </Button>
                            </td>
                          </tr>
                        </Fragment>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          </DashboardContainer>
        </>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const SchoolsList = connect(mapStateToProps, {
  FC_GetAllPositions,
  FC_AllSchoolHierarchyDetails,
})(_SchoolsList);
